import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormGroupDirective } from '@angular/forms';
import { MatDialog, MatTableDataSource } from '@angular/material';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { DeleteComponent } from '../delete/delete.component';
import { DialogueComponent } from '../dialogue/dialogue.component';
import { Globalconstants } from '../globalconstants';
import { OnlineStudentProfile } from '../model/onlineStudentProfile';
import { SnackbarService } from '../service/snackbar.service';
import { TabServices } from '../service/tabServices';

@Component({
  selector: 'app-admission',
  templateUrl: './admission.component.html',
  styleUrls: ['./admission.component.css']
})
export class AdmissionComponent implements OnInit {

  admissionForm: FormGroup;
  registerForm: FormGroup;
  title: string;
  description: string;
  buttonText: string;
  procedures_title: string;
  procedures_description: string;
  student_Departments: null;
  student_Registeration_Status: null;

  admissionContent: boolean = false;
  viewRegister: boolean = false;
  editHeader: boolean = false;
  descriptionHeader: boolean = false;
  form: boolean = false;
  onlineRegister: boolean = false;

  public mat_DataSource = new MatTableDataSource<OnlineStudentProfile>();

  constructor(private snackBar: SnackbarService,
    private spinner: NgxSpinnerService,
    private tabSerives: TabServices,
    private dialog: MatDialog,
    private router: Router,
    private formBuilder: FormBuilder,
  ) {
    const onlineStudentProfile: OnlineStudentProfile[] = [];
    this.mat_DataSource = new MatTableDataSource(onlineStudentProfile);
  }

  ngOnInit() {

    this.formValidation();
    this.registerformValidation();
    this.adminValid();
    this.getSchoolAdmissionHeader();
    this.studentDepartments();
    this.studentRegistrationStatus();
  }

  studentDepartments() {
    this.tabSerives.getStudentDepartments().subscribe((data) => {
      this.student_Departments = data;
    });
  }

  studentRegistrationStatus() {
    this.tabSerives.getStudentRegistrationStatus().subscribe((data) => {
      this.student_Registeration_Status = data;
    });
  }

  adminValid() {
    if (localStorage.getItem('profileCategory') === '1') {
      this.editHeader = true;
      this.viewRegister = true;

    }
    this.admissionContent = true;
    this.descriptionHeader = false;
    this.onlineRegister = false;
  }

  emailExist(email) {
    const validEmailRegEx = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (validEmailRegEx.test(email)) {

      this.tabSerives.emailExist(email).subscribe((data) => {
        console.log(data)
        if (data === true) {
          this.snackBar.openSnackBarWarn("Email ID already exist", "");
        }
      });

    }
  }

  mobileExist(mobile) {
    this.tabSerives.mobileExist(mobile).subscribe((data) => {
      console.log(data)
      if (data === true) {
        this.snackBar.openSnackBarWarn("Mobile number already exist", "");
      }

    });
  }

  editAdmissionHeader() {
    this.onlineRegister = false;
    this.admissionContent = false;
    this.descriptionHeader = true;
  }

  deletePrograms(programs) {

    const dialogRef = this.dialog.open(DeleteComponent, {

      width: Globalconstants.width_delete,
      height: Globalconstants.height_delete,
      data: {
        programs_id: programs.id,
        component: Globalconstants.delete_programs
      }

    });
    dialogRef.afterClosed().subscribe(result => {
      //console.log(result);
      // this.router.navigateByUrl('/home/admission');
      // this.getSchoolAdmissionHeader();
      this.pageRefresh();
    });
  }

  cancel() {
    this.adminValid();
    // this.pageRefresh();
  }

  pageRefresh() {
    this.router.routeReuseStrategy.shouldReuseRoute = () => false;
    this.router.onSameUrlNavigation = 'reload';
    this.router.navigateByUrl('/home/admission');
  }

  formValidation() {

    this.admissionForm = this.formBuilder.group({
      title: ['', Validators.required],
      description: ['', Validators.required],
      procedures_title: ['', Validators.required],
      procedures_description: ['', Validators.required]
    });
  }

  onlineRegisteration(adminssion, page) {
    this.onlineRegister = true;
    this.admissionContent = false;
    this.descriptionHeader = false;

    if (page === 'edit') {
      this.admissionForm.patchValue(adminssion);
      this.buttonText = 'Update';
    }
    else {
      this.admissionForm.reset();
      this.buttonText = 'Add';
    }
  }

  onRegistrationForm(formDirective: FormGroupDirective, page) {

    this.spinner.show();

    this.tabSerives.onlineRegistration(this.registerForm.value).subscribe((data) => {
      console.log(data)
      if (data.profile_id != null) {

        this.spinner.hide();
        formDirective.resetForm();
        this.registerForm.reset();

        if (page == 'add') {

          this.dialogue(data.profile_id,
            data.student_name,
            data.father_name);
        } else if (page == 'edit') {

          localStorage.setItem('page', Globalconstants.edit_profile)
          this.router.navigateByUrl('/home/profile');

        }

      } else {
        this.snackBar.openSnackBarError("Profile is already registered, Please check your mobile number and email ID", "")
        this.spinner.hide();
      }


    }),
      error => {
        console.log(error)
        this.snackBar.openSnackBarError("Unable to process the webservice", "")
        this.spinner.hide();
      },
      () => { };

  }

  getStudentOnlineRegistrations() {
    this.tabSerives.getOnlineStudentRegistration().subscribe((data) => {
      this.mat_DataSource.data = data as OnlineStudentProfile[];
      console.log(data);

    });
  }

  dialogue(profile_id, first_name, last_name) {
    this.dialog.open(DialogueComponent, {
      width: Globalconstants.width_reponse,
      height: Globalconstants.height_reponse,
      data: {
        profile_id: profile_id, first_name: first_name, last_name: last_name
      }
    }),
      this.dialog.afterAllClosed.subscribe(result => {

      });
  }

  registerformValidation() {

    this.registerForm = this.formBuilder.group({
      id: [''],
      student_name: ['', Validators.required],
      father_name: ['', Validators.required],
      dob: ['', Validators.required],
      mobile_no: ['', Validators.pattern('[6-9]\\d{9}')],
      email_id: ['', Validators.pattern("[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{2,}")],
      dept: this.formBuilder.group({
        dept_id: ['', [Validators.required]]
      }),
      regStatus: ['', ''],
    });
  }

  getSchoolAdmissionHeader() {
    this.tabSerives.getSchoolAdmissionHeader().subscribe((data) => {
      this.title = data.title;
      this.description = data.description;
      this.procedures_title = data.procedures_title;
      this.procedures_description = data.procedures_description;
      this.admissionForm.patchValue(data);
      this.spinner.hide();
    });
  }

  close() {
    this.adminValid();
  }

  onSubmit(formDirective: FormGroupDirective) {

    this.spinner.show();

    this.tabSerives.updateSchoolAdmissionHeader(this.admissionForm.value).subscribe((data) => {
      if (data.id != null) {

        this.spinner.hide();
        formDirective.resetForm();
        this.admissionForm.reset();
        this.adminValid();
        // this.getSchoolAdmissionHeader();
        this.pageRefresh();

        // this.router.navigateByUrl('/home/admission');

      } else {
        this.snackBar.openSnackBarError("Failure in updating data", "");
        this.spinner.hide();
      }
    }),
      error => {
        console.log(error)
        this.snackBar.openSnackBarError("Unable to process the webservice", "");
        this.spinner.hide();
      },
      () => { };

  }

}
