import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidationErrors, ValidatorFn, FormGroupDirective } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService } from '../auth/auth.service';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { Globalconstants } from '../globalconstants';
import { ProfileService } from '../service/profileService';
import { NgxSpinnerService } from 'ngx-spinner';
import { SnackbarService } from '../service/snackbar.service';
import { DialogueComponent } from '../dialogue/dialogue.component';
import { DatePipe } from '@angular/common';
import { ProfileComponent } from '../profile/profile.component';
import { serialize } from 'object-to-formdata';
import { HttpEventType, HttpResponse } from '@angular/common/http';
import { Subscription } from 'rxjs';
import { OnDestroy } from '@angular/core';

@Component({
  selector: 'app-create-accounts',
  templateUrl: './create-accounts.component.html',
  styleUrls: ['./create-accounts.component.css']
})
export class CreateAccountsComponent implements OnInit, OnDestroy {

  buttonText: string;
  show: boolean;
  minPw = 1;
  department_Type = null;
  registerTitle: String;
  content: String;
  registerForm: FormGroup;
  profile_Category = null;
  emailError = false;
  public mobileError: boolean;
  private component: string;
  public editable: boolean = false;
  hidePassword: any
  hidePassword1: any
  hidePasswordconfirm: any

  public imagePath;
  imgURL: any;
  selectedFiles: FileList;
  currentFileUpload: File;
  private subscription: Subscription = new Subscription();

  constructor(private formBuilder: FormBuilder,
    private router: Router,
    public authService: AuthService,
    public dialog: MatDialog,
    private spinner: NgxSpinnerService,
    private profileService: ProfileService,
    private snackBar: SnackbarService,
    private route: ActivatedRoute,
    private datepipe: DatePipe,
    public profileComp: ProfileComponent) { }

  ngOnInit() {

    this.constants();
    this.profileType();
    this.formValidation();
    this.pageCheck();

  }

  setDefaultPic(event) {
    event.target.src = Globalconstants.defaultFilePath;
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  preview(files) {
    if (files.length === 0)
      return;

    var mimeType = files[0].type;
    if (mimeType.match(/image\/*/) == null) {
      this.snackBar.openSnackBarWarn(Globalconstants.fileType, '');
      return;
    }

    if (files[0].size > Globalconstants.imagesize) {
      this.snackBar.openSnackBarWarn(Globalconstants.errorFileSize, '');

      return false;
    }

    var reader = new FileReader();
    this.imagePath = files;
    reader.readAsDataURL(files[0]);
    reader.onload = (_event) => {
      this.imgURL = reader.result;
      //this.registerForm.get('profileImage').setValue(files);
      this.selectedFiles = files;
    }
  }

  pageCheck() {

    this.route.queryParams.subscribe(
      params => {

        this.component = params['component'];

        if (this.component == '2') {

          this.show = !this.show;
          this.buttonText = 'Sign up';
          this.editable = false;
        } else if (this.component == '3') {
          this.editable = true;
          this.buttonText = 'Update';
          this.show = this.show;
          let data = JSON.parse(params['profile']);
          this.department(data.profile_catg.profile_category_id);
          this.imgURL = data.profile_url;
          data.passwordConfirm = data.password;
          data.dob = this.datepipe.transform(data.dob, 'yyyy-MM-dd');
          this.registerForm.patchValue(data)

        }
      })

  }

  constants() {
    this.registerTitle = Globalconstants.sign_up
    this.content = Globalconstants.content
  }

  formValidation() {

    this.registerForm = this.formBuilder.group({
      id: [''],
      profile_id: [''],
      first_name: ['', Validators.required],
      last_name: ['', Validators.required],
      password: ['', [Validators.required, Validators.minLength(this.minPw)]],
      passwordConfirm: ['', [Validators.required]],
      dob: ['', Validators.required],
      mobile_no: ['', Validators.pattern('[6-9]\\d{9}')],
      email_id: ['', Validators.pattern("[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{2,}")],
      profile_url: ['',],
      profile_catg: this.formBuilder.group({
        profile_category_id: ['', [Validators.required]]
      }),
      dept: this.formBuilder.group({
        dept_id: ['', [Validators.required]]
      }),
    }, { validator: passwordMatchValidator });

  }

  profileType() {
    this.profileService.getProfileType().subscribe((data) => {
      this.profile_Category = data;
      this.spinner.hide();
    });
  }

  department(profille_category_id) {
    this.department_Type = null;
    this.spinner.show();
    this.profileService.getDepartments(profille_category_id).subscribe((data) => {
      this.department_Type = data;
      this.spinner.hide();
    });
  }

  emailExist(email) {
    const validEmailRegEx = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    if (validEmailRegEx.test(email)) {

      this.profileService.emailExist(email).subscribe((data) => {

        if (data === true) {
          this.snackBar.openSnackBarWarn("Email ID already exist", "");
        }
      });

    }
  }

  mobileExist(mobile) {
    this.profileService.mobileExist(mobile).subscribe((data) => {

      if (data === true) {
        this.snackBar.openSnackBarWarn("Mobile number already exist", "");
      }

    });
  }

  onSubmit(formDirective: FormGroupDirective) {

    this.spinner.show();

    if (this.selectedFiles && this.selectedFiles.length > 0) {
      this.registerForm.get("profile_url").setValue("yes")
    }
    else {
      this.registerForm.get("profile_url").setValue("no")
    }
    this.profileService.getRegister(this.registerForm.value).subscribe((data) => {
      if (data.id != null) {

        this.spinner.hide();
        formDirective.resetForm();
        this.registerForm.reset();
        if (this.selectedFiles && this.selectedFiles.length > 0) {
          this.upload(data.profile_id);
        } else {
          console.log(Globalconstants.noFile)
        }
        if (this.component == '2') {
          this.dialogue(data.profile_id, data.first_name, data.last_name);
        } else if (this.component == '3') {
          localStorage.setItem('page', Globalconstants.edit_profile)
          this.router.navigateByUrl('/home/profile');

        }
      } else {
        this.snackBar.openSnackBarError("Please check your mobile number and email ID", "")
        this.spinner.hide();
      }


    }),
      error => {
        console.log(error)
        this.snackBar.openSnackBarError("Unable to process the webservice", "")
        this.spinner.hide();
      },
      () => { };

  }

  upload(profile_id) {
    
    //check file size less than 10 MB
    if ((this.selectedFiles.item(0).size / 1024 / 1024 < 10)) {
      this.spinner.show();
      this.currentFileUpload = this.selectedFiles.item(0);
      this.subscription.add(this.profileService.pushFileToStorage(this.currentFileUpload, profile_id, Globalconstants.profileFolder)
        .subscribe(
          event => {
            this.spinner.hide();

            if (event.type === HttpEventType.UploadProgress) {
              // this.progress.percentage = Math.round(100 * event.loaded / event.total);
            } else if (event instanceof HttpResponse) {
              this.imgURL = Globalconstants.defaultFilePath;
            }

            this.selectedFiles = undefined;
          }, error => {

            this.spinner.hide();
            this.snackBar.openSnackBarError(Globalconstants.errorMessage, "")
          }));
    }
    else {
      this.snackBar.openSnackBarWarn(Globalconstants.imageSizeError, '');
    }
  }

  cancel() {
    if (this.component == '2') {
      this.router.navigateByUrl('/home/profile');
    } else if (this.component == '3') {
      localStorage.setItem('page', Globalconstants.edit_profile)
      this.router.navigateByUrl('/home/profile');

    }
  }

  dialogue(profile_id, first_name, last_name) {
    this.dialog.open(DialogueComponent, {
      width: Globalconstants.width_reponse,
      height: Globalconstants.height_reponse,
      data: {
        profile_id: profile_id, first_name: first_name, last_name: last_name
      }
    }),
      this.dialog.afterAllClosed.subscribe(result => {

      });
  }

  get password() { return this.registerForm.get('password'); }
  get passwordConfirm() { return this.registerForm.get('passwordConfirm'); }

  onPasswordInput() {
    if (this.registerForm.hasError('passwordMismatch'))
      this.passwordConfirm.setErrors([{ 'passwordMismatch': true }]);
    else
      this.passwordConfirm.setErrors(null);
  }

}

export const passwordMatchValidator: ValidatorFn = (formGroup: FormGroup): ValidationErrors | null => {
  if (formGroup.get('password').value === formGroup.get('passwordConfirm').value)
    return null;
  else
    return { passwordMismatch: true };
};
