import { Injectable, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, HttpRequest } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Globalconstants } from '../globalconstants';

const httpOptions = {
    headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
    })
};

@Injectable({
    providedIn: 'root'
})
export class ProfileService {
    public baseUrl = "https://schoolmenu.azurewebsites.net/schoolmenu";
    // public baseUrl = "https://localhost:5000/school";
    constructor(private httpClient: HttpClient) { }

    public getProfileType() {
        return this.httpClient.get(this.baseUrl+'/profile_category/');
    }

    public getDepartments(admin_id: number) {
        return this.httpClient.get(this.baseUrl+'/departments/' + admin_id);
    }

    public getProfiles(profile_values: any) {
        return this.httpClient.post<any>(this.baseUrl+'/profiles/', profile_values, httpOptions);
    }

    public getLogin(loginCredential) {

        // return this.httpClient.get(`http://localhost:8080/login/`);
        return this.httpClient.post<any>(this.baseUrl+'/login/', loginCredential, httpOptions);

    }

    public getRegister(userDetails) {

        return this.httpClient.post<any>(this.baseUrl+'/register/', userDetails, httpOptions);
    }

    public mobileExist(mobile_no: number) {
        return this.httpClient.get(this.baseUrl+'/mobile/' + mobile_no);
    }

    public emailExist(email_id: string) {
        return this.httpClient.get(this.baseUrl+'/email/' + email_id);
    }

    pushFileToStorage(file: File, profile_id, profileFolder): Observable<any> {
        let headers = new HttpHeaders();
        headers = headers.append('X-Content-Type-Options', 'nosniff');
        const data: FormData = new FormData();
        data.append('file', file);
        data.append('profile_Id', profile_id);
        data.append('profile_Folder', profileFolder);
        const newRequest = new HttpRequest('POST', this.baseUrl+'/uploadImages/', data, {
            headers: headers,
            reportProgress: true

        });
        return this.httpClient.request(newRequest);
    }

}


