import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { DeleteComponent } from '../delete/delete.component';
import { CommonServices } from '../service/commonServices';
import { SnackbarService } from '../service/snackbar.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.css']
})
export class EditComponent implements OnInit {

  profile_id: string;
  name: string;
  profile_category_id: number;
  componentId: number;
  messsage: string;

  constructor(private dialogRef: MatDialogRef<EditComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private deleteService: CommonServices,
    private spinner: NgxSpinnerService,
    private snackBar: SnackbarService,
    private router: Router) {
    dialogRef.disableClose = true;
  }

  ngOnInit() {
    this.profileData();
  }

  profileData() {

    if (this.data.component = '1') {
      this.profile_id = this.data.profile_id;
      this.profile_category_id = this.data.profile_category_id;
      this.name = this.data.first_name + " " + this.data.last_name;
      this.messsage = 'Do you want to delete the profile ID ' + this.profile_id
        + ' - ' + this.name
    }
  }

}
