import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { AuthGuard } from './auth/auth.guard';
import { SchoolHomeComponent } from './school-home/school-home.component';
import { AboutComponent } from './about/about.component';
import { ProgramsComponent } from './programs/programs.component';
import { StudentDevelopmentsComponent } from './student-developments/student-developments.component';
import { CareersComponent } from './careers/careers.component';
import { ReachusComponent } from './reachus/reachus.component';
import { AdmissionComponent } from './admission/admission.component';
import { CreateAccountsComponent } from './create-accounts/create-accounts.component';
import { TimetableComponent } from './timetable/timetable.component';
import { LeaveComponent } from './leave/leave.component';
import { BusRouteComponent } from './bus-route/bus-route.component';
import { HolidayListComponent } from './holiday-list/holiday-list.component';
import { UpdateComponent } from './update/update.component';
import { ProfileComponent } from './profile/profile.component';
import { DialogueComponent } from './dialogue/dialogue.component';
import { DeleteComponent } from './delete/delete.component';
import { EditComponent } from './edit/edit.component';
const appRoutes: Routes = [
  {
    path: "home", component: HomeComponent,
    children: [
      {
        path: 'schoolHome', component: SchoolHomeComponent,
        children: [{
          path: 'dialogue', component: DialogueComponent
        }]
      },
      { path: 'about', component: AboutComponent },
      { path: 'programs', component: ProgramsComponent },
      { path: 'studev', component: StudentDevelopmentsComponent },
      { path: 'admission', component: AdmissionComponent },
      { path: 'careers', component: CareersComponent },
      { path: 'reachus', component: ReachusComponent },
      {
        path: 'profile', component: ProfileComponent,
        children: [{ path: 'delete', component: DeleteComponent },
        {
          path: 'edit', component: EditComponent
        }]
      },
      { path: 'createaccount', component: CreateAccountsComponent },
      { path: 'timetable', component: TimetableComponent },
      { path: 'leave', component: LeaveComponent },
      { path: 'busroutes', component: BusRouteComponent },
      { path: 'holidaylist', component: HolidayListComponent },
      { path: 'update', component: UpdateComponent },
      { path: 'login', component: LoginComponent }
    ],
  },

  { path: '', pathMatch: 'full', redirectTo: '/home/schoolHome' }

];
@NgModule({
  imports: [RouterModule.forRoot(appRoutes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
