import { HttpEventType, HttpResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormGroupDirective } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { Subscription } from 'rxjs';
import { Globalconstants } from '../globalconstants';
import { ProfileService } from '../service/profileService';
import { SnackbarService } from '../service/snackbar.service';
import { TabServices } from '../service/tabServices';

@Component({
  selector: 'app-school-home',
  templateUrl: './school-home.component.html',
  styleUrls: ['./school-home.component.css']
})
export class SchoolHomeComponent implements OnInit {

  homeForm: FormGroup;
  public imagePath;
  imgURL: any;
  selectedFiles: FileList;
  currentFileUpload: File;
  private subscription: Subscription = new Subscription();
  title: string;
  description: string;
  news: string;

  edit: boolean = false;
  showedit: boolean = false;

  constructor(private snackBar: SnackbarService,
    private spinner: NgxSpinnerService,
    private profileService: ProfileService,
    private tabSerives: TabServices,
    private router: Router,
    private formBuilder: FormBuilder) { }

  ngOnInit() {
    if (localStorage.getItem('profileCategory') === '1') {

      this.showedit = true;

    }
    this.formValidation();
    this.getSchoolProfile();
  }

  editHome() {
    this.getSchoolProfile();
    this.edit = true;
  }

  cancel() {
    this.edit = false;
  }

  formValidation() {

    this.homeForm = this.formBuilder.group({
      id: [''],
      title: ['', Validators.required],
      description: ['', Validators.required],
      news: ['', '']
    });
  };

  setDefaultPic(event) {
    event.target.src = Globalconstants.defaultFilePath;
  }

  preview(files) {
    if (files.length === 0)
      return;

    var mimeType = files[0].type;
    if (mimeType.match(/image\/*/) == null) {
      this.snackBar.openSnackBarWarn(Globalconstants.fileType, '');
      return;
    }

    if (files[0].size > Globalconstants.imagesize) {
      this.snackBar.openSnackBarWarn(Globalconstants.errorFileSize, '');

      return false;
    }

    var reader = new FileReader();
    this.imagePath = files;
    reader.readAsDataURL(files[0]);
    reader.onload = (_event) => {
      this.imgURL = reader.result;
      //this.registerForm.get('profileImage').setValue(files);
      this.selectedFiles = files;
    }
  }

  getSchoolProfile() {
    this.tabSerives.getSchoolProfile().subscribe((data) => {

      this.title = data.title;
      this.description = data.description;
      this.news = data.news;
      this.imgURL = data.home_image_url;
      this.homeForm.patchValue(data);
      this.spinner.hide();
    });
  }

  onSubmit(formDirective: FormGroupDirective) {

    this.spinner.show();

    this.tabSerives.updateSchoolProfile(this.homeForm.value).subscribe((data) => {
      if (data.id != null) {

        this.spinner.hide();
        formDirective.resetForm();
        this.homeForm.reset();
        this.edit = false;
        this.getSchoolProfile();
        // this.upload("SchoolProfile" + data.id);
        this.upload(Globalconstants.schoolProfileFolder);
        this.router.navigateByUrl('/home/schoolHome');

      } else {
        this.snackBar.openSnackBarError("Failure in updating data", "")
        this.spinner.hide();
      }
    }),
      error => {
        console.log(error)
        this.snackBar.openSnackBarError("Unable to process the webservice", "")
        this.spinner.hide();
      },
      () => { };

  }

  upload(id) {

    // if ((this.selectedFiles.item(0).size / 1024 / 1024 < 10) && (this.selectedFiles.item(0).type == 'application/vnd.ms-excel' || this.selectedFiles.item(0).type == 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' || this.selectedFiles.item(0).type == 'application/octet-stream' || this.selectedFiles.item(0).type == 'application/vnd.oasis.opendocument.spreadsheet' || this.selectedFiles.item(0).type == "")) {
    if ((this.selectedFiles.item(0).size / 1024 / 1024 < 10)) {
      this.spinner.show();
      // this.progress.percentage = 0;
      this.currentFileUpload = this.selectedFiles.item(0);
      this.subscription.add(this.profileService.pushFileToStorage(this.currentFileUpload, id, Globalconstants.schoolProfileFolder)
        .subscribe(
          event => {
            this.spinner.hide();

            if (event.type === HttpEventType.UploadProgress) {
              // this.progress.percentage = Math.round(100 * event.loaded / event.total);
            } else if (event instanceof HttpResponse) {
              this.imgURL = Globalconstants.defaultFilePath;
            }

            // this.selectedFiles = undefined;
          }, error => {

            this.spinner.hide();
            this.snackBar.openSnackBarError(Globalconstants.errorMessage, "")
          }));
    }
    else {
      this.snackBar.openSnackBarWarn(Globalconstants.errorMessage, '');
    }
  }
}
