import { Route } from '@angular/compiler/src/core';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { DomSanitizer } from '@angular/platform-browser';
import { NgxSpinnerService } from 'ngx-spinner';
import { Vehicle } from '../model/vehicle';
import { BusRoute } from '../service/busRoute';

@Component({
  selector: 'app-bus-route',
  templateUrl: './bus-route.component.html',
  styleUrls: ['./bus-route.component.css']
})
export class BusRouteComponent implements OnInit {

  showPanel: boolean = false;
  url: string
  controllerSrc: any;
  vehicle: any;
  bus_no: any;
  news: any;
  createProfile: any;

  public route_DataSource = new MatTableDataSource<Vehicle>();

  private paginator: MatPaginator;
  @ViewChild(MatPaginator, { static: false }) set matPaginator(mp: MatPaginator) {
    this.paginator = mp;
    this.route_DataSource.paginator = this.paginator;
  }

  // @ViewChild(MatPaginator, { static: false }) paginator
  @ViewChild(MatSort, { static: false }) sort



  public Columns = [
    'bus_no',
    'first_name',
    'last_name',
    'mobile_no',
    'route',
    //'lat',
    //'lon',
    'map'
  ];

  constructor(
    private vehicleService: BusRoute,
    private spinner: NgxSpinnerService,
    private sanitizer: DomSanitizer
  ) {
    const route: Vehicle[] = [];
    this.route_DataSource = new MatTableDataSource(route);
  }

  ngOnInit() {
    this.showPanel = true
    this.vehicles()
  }

  ngAfterViewInit() {
    this.route_DataSource.paginator = this.paginator;
    this.route_DataSource.sort = this.sort;
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.route_DataSource.filter = filterValue;
  }

  vehicles() {
    this.vehicleService.vehicle().subscribe((data) => {
      this.route_DataSource.data = data as Vehicle[];
      this.spinner.hide();
    });
  }

  mapRefresh() {
    this.vehicleService.mapRefresh(this.bus_no).subscribe((data) => {
      this.vehicle = data;
      console.log(data)
      this.mapView(this.vehicle.bus_no, this.vehicle.lat, this.vehicle.lon)
      this.spinner.hide();
    });
  }

  mapView(bus_no: number, lat: number, lon: number) {
    this.bus_no = bus_no;
    this.showPanel = false
    this.controllerSrc = this.getSafeUrl(`https://www.google.com/maps?q=${lat},${lon}&output=embed`);


  }

  getSafeUrl(url) {
    return this.sanitizer.bypassSecurityTrustResourceUrl(url)
  }

  onNoClick() {
    this.showPanel = true
  }

}