
import { HttpClient, HttpHeaders, HttpParams, HttpRequest } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';

const httpOptions = {
    headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
    })
};

@Injectable({
    providedIn: 'root'
})
export class TabServices {
    
    // public baseUrl = environment.production;
    public baseUrl = "https://schoolweb.azurewebsites.net";
    constructor(private httpClient: HttpClient) { }

    public getSchoolProfile() {
        return this.httpClient.get<any>(this.baseUrl+'/schooltabs/getSchoolProfile/');
    }

    public updateSchoolProfile(userDetails) {

        return this.httpClient.post<any>(this.baseUrl+'/schooltabs/schoolProfile/', userDetails, httpOptions);
    }

    public getAboutProfile() {
        return this.httpClient.get<any>(this.baseUrl+'/schooltabs/getSchoolAbout/');
    }

    public updateAboutProfile(userDetails) {

        return this.httpClient.post<any>(this.baseUrl+'/schooltabs/schoolAbout/', userDetails, httpOptions);
    }

    public getSchoolPrograms() {
        return this.httpClient.get<any>(this.baseUrl+'/schooltabs/getPrograms/');
    }

    public updateSchoolPrograms(userDetails) {

        return this.httpClient.post<any>(this.baseUrl+'/schooltabs/updatePrograms/', userDetails, httpOptions);
    }

    public getSchoolStudentDevelopment() {
        return this.httpClient.get<any>(this.baseUrl+'/schooltabs/getStudentDevelopment/');
    }

    public updateSchoolStudentDevelopment(userDetails) {

        return this.httpClient.post<any>(this.baseUrl+'/schooltabs/updateStudentDevelopment/', userDetails, httpOptions);
    }

    public getSchoolAdmissionHeader() {
        return this.httpClient.get<any>(this.baseUrl+'/schooltabs/getAdmissionDetailsHeader/');
    }

    public updateSchoolAdmissionHeader(userDetails) {

        return this.httpClient.post<any>(this.baseUrl+'/schooltabs/updateAdmissionDetailsHeader/', userDetails, httpOptions);
    }

    public getStudentDepartments() {
        return this.httpClient.get<any>(this.baseUrl+'/schooltabs/getStudentDepartments/');
    }

    public onlineRegistration(userDetails) {

        return this.httpClient.post<any>(this.baseUrl+'/schooltabs/onlineRegistration/', userDetails, httpOptions);
    }

    public mobileExist(mobile_no: number) {
        return this.httpClient.get(this.baseUrl+'/schooltabs/mobile/' + mobile_no);
    }

    public emailExist(email_id: string) {
        return this.httpClient.get(this.baseUrl+'/schooltabs/email/' + email_id);
    }

    public getStudentRegistrationStatus() {
        return this.httpClient.get<any>(this.baseUrl+'/schooltabs/getStudentRegistrationStatus/');
    }

    public getOnlineStudentRegistration() {
        return this.httpClient.get<any>(this.baseUrl+'/schooltabs/getStudent_Online_Registrations/');
    }

}


