import { HttpEventType, HttpResponse } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormGroupDirective } from '@angular/forms';
import { MatDialog } from '@angular/material';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { Subscription } from 'rxjs';
import { DeleteComponent } from '../delete/delete.component';
import { Globalconstants } from '../globalconstants';
import { ProfileService } from '../service/profileService';
import { SnackbarService } from '../service/snackbar.service';
import { TabServices } from '../service/tabServices';

@Component({
  selector: 'app-student-developments',
  templateUrl: './student-developments.component.html',
  styleUrls: ['./student-developments.component.css']
})
export class StudentDevelopmentsComponent implements OnInit {

  programsForm: FormGroup;
  programTitle: string;
  programDescription: string;
  public imagePath;
  imgURL: any;
  selectedFiles: FileList;
  currentFileUpload: File;
  private subscription: Subscription = new Subscription();
  title: string;
  description: string;
  news: string;
  programData: any;
  buttonValue: string;

  edit: boolean = false;
  showedit: boolean = false;
  imageDetails: boolean = false;
  gridDiv: boolean = false;

  constructor(private snackBar: SnackbarService,
    private spinner: NgxSpinnerService,
    private profileService: ProfileService,
    private tabSerives: TabServices,
    private dialog: MatDialog,
    private router: Router,
    private formBuilder: FormBuilder) { }

  ngOnInit() {

    this.adminValid();
    this.formValidation();
    this.getSchoolPrograms();

  }

  adminValid() {
    this.gridDiv = true;
    if (localStorage.getItem('profileCategory') === '1') {

      this.showedit = true;

    }
  }

  editStudentDevelopments(studentDevelopment, page) {
    // this.getSchoolPrograms();
    this.edit = true;
    this.imageDetails = false;
    this.gridDiv = false;
    this.showedit = false;

    console.log(studentDevelopment)

    this.imgURL = studentDevelopment.student_development_image_url;
    if (page === 'edit') {
      this.programsForm.patchValue(studentDevelopment);
      this.buttonValue = 'Update';
    }
    else {
      this.programsForm.reset();
      this.buttonValue = 'Add';
    }
  }

  deleteStudentDevelopments(studentDevelopments) {

    const dialogRef = this.dialog.open(DeleteComponent, {

      width: Globalconstants.width_delete,
      height: Globalconstants.height_delete,
      data: {
        programs_id: studentDevelopments.id,
        component: Globalconstants.delete_student_developments
      }

    });
    dialogRef.afterClosed().subscribe(result => {
      //console.log(result);
      this.router.navigateByUrl('/home/studev');
      this.getSchoolPrograms();
    });
  }

  cancel() {
    this.edit = false;
    this.adminValid();
    this.imageDetails = false;
    this.gridDiv = true;

  }

  formValidation() {

    this.programsForm = this.formBuilder.group({
      id: [''],
      title: ['', Validators.required],
      description: ['', Validators.required]
    });
  };

  setDefaultPic(event) {
    event.target.src = Globalconstants.defaultFilePath;
  }

  preview(files) {
    if (files.length === 0)
      return;

    var mimeType = files[0].type;
    if (mimeType.match(/image\/*/) == null) {
      this.snackBar.openSnackBarWarn(Globalconstants.fileType, '');
      return;
    }

    if (files[0].size > Globalconstants.imagesize) {
      this.snackBar.openSnackBarWarn(Globalconstants.errorFileSize, '');

      return false;
    }

    var reader = new FileReader();
    this.imagePath = files;
    reader.readAsDataURL(files[0]);
    reader.onload = (_event) => {
      this.imgURL = reader.result;
      //this.registerForm.get('profileImage').setValue(files);
      this.selectedFiles = files;
    }
  }

  getSchoolPrograms() {
    this.tabSerives.getSchoolStudentDevelopment().subscribe((data) => {

      this.programData = data;
      this.title = data.title;
      this.description = data.description;
      // this.news = data.news;
      this.imgURL = data.student_development_image_url;
      this.programsForm.patchValue(data);
      this.spinner.hide();

    });
  }

  studentDevelopmentsDetail(programs) {
    this.programTitle = programs.title;
    this.programDescription = programs.description;
    this.edit = false;
    this.showedit = false;
    this.imageDetails = true;
    this.gridDiv = false;
  }

  close() {
    this.imageDetails = false;
    // this.showedit = true;
    this.edit = false;
    // this.gridDiv = true;
    this.adminValid();
  }

  onSubmit(formDirective: FormGroupDirective) {

    this.spinner.show();

    this.tabSerives.updateSchoolStudentDevelopment(this.programsForm.value).subscribe((data) => {
      if (data.id != null) {

        this.spinner.hide();
        formDirective.resetForm();
        this.programsForm.reset();
        this.imageDetails = false;
        this.edit = false;
        // this.gridDiv = true;
        this.adminValid();
        this.getSchoolPrograms();

        this.upload(Globalconstants.schoolStudentDevelopmentFolder + data.id);
        this.router.navigateByUrl('/home/studev');

      } else {
        this.snackBar.openSnackBarError("Failure in updating data", "")
        this.spinner.hide();
      }
    }),
      error => {
        console.log(error)
        this.snackBar.openSnackBarError("Unable to process the webservice", "")
        this.spinner.hide();
      },
      () => { };

  }

  upload(id) {

    // if ((this.selectedFiles.item(0).size / 1024 / 1024 < 10) && (this.selectedFiles.item(0).type == 'application/vnd.ms-excel' || this.selectedFiles.item(0).type == 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' || this.selectedFiles.item(0).type == 'application/octet-stream' || this.selectedFiles.item(0).type == 'application/vnd.oasis.opendocument.spreadsheet' || this.selectedFiles.item(0).type == "")) {
    if ((this.selectedFiles.item(0).size / 1024 / 1024 < 10)) {
      this.spinner.show();
      this.currentFileUpload = this.selectedFiles.item(0);
      this.subscription.add(this.profileService.pushFileToStorage(this.currentFileUpload, id, Globalconstants.schoolStudentDevelopmentFolder)
        .subscribe(
          event => {
            this.spinner.hide();

            if (event.type === HttpEventType.UploadProgress) {
            } else if (event instanceof HttpResponse) {
              this.imgURL = Globalconstants.defaultFilePath;
            }

          }, error => {

            this.spinner.hide();
            this.snackBar.openSnackBarError(Globalconstants.errorMessage, "")
          }));
    }
    else {
      this.snackBar.openSnackBarWarn(Globalconstants.errorMessage, '');
    }
  }

}
